///<amd-module name='ModuleDontChangeThisLine' />
import * as React from 'react';
import { createElement as el } from 'react';

import {
    DropdownWidget,
    DropdownType,
    Block,
    InlineBlock,
    TabStyles,
    ButtonType,
    ButtonWidget,
    CheckBoxWithLabelWidget,
    ActivitySpinnerWidget,
} from '@tableau/widgets';
  
const { Spacing } = TabStyles;

const Styles = {
    dropdownContainer: {
        marginLeft:0,
        width: '100%'
    }
};
  
interface DropdownState {
    currentVal: string;
}

interface DropdownProps {
    list: any;
    currentVal: any;
    disabled: any;
    onChange: any;
}
  
class OutlineDropdown extends React.Component<DropdownProps, DropdownState>  {
    protected boundOnSelect: (value: string) => void = value => this.props.onChange(value);

    public constructor(props: DropdownProps) {
        super(props);
        this.state = {
            currentVal: this.props.currentVal,
        };
    }

    public render(): JSX.Element {
        return (
            el(
                InlineBlock, {style: Styles.dropdownContainer},
                el(DropdownWidget, {
                    zIndex: 1031,
                    label: '',
                        containerStyle: {
                            width: '100%',
                            flex: 1
                        },
                    selectedValue: this.props.currentVal,
                    onSelect: this.boundOnSelect,
                    dropdownType: DropdownType.Line,
                    maxHeight: 200,
                    testId: 'select-outline',
                    menuItemInfos: this.props.list,
                    disabled: this.props.disabled
                })
            )
        );
    };
};

declare global {
    interface Window { tableau: any; }
}

let dashboard, dataSource, pset, dsnames, dslist, wslist;

// Shows if setting has not yet been configured
class Selector extends React.Component<any, any> {
    render() {
        return (
            <div className='controls'>
                <OutlineDropdown list={this.props.list} currentVal={this.props.selected} disabled={!this.props.enabled} onChange={this.props.onChange}/>
                <ButtonWidget buttonType={ButtonType.Go} handleClick={this.props.onClick} testId={this.props.selecting + 'set'} disabled={!this.props.enabled || this.props.selected === ''} style={{marginLeft: '12px'}}>Set</ButtonWidget>
            </div>
        )
    }
}

// Shows if setting has already been configured
function Selected(props) {
    return (
        <div className={'selected ' + (props.clear || '')}>
        <p><i>The {props.selecting} <b>{props.selected}</b> has been selected</i>{props.clear === 'clear' ? <a onClick={props.clearWS}>
        <svg xmlns='http://www.w3.org/2000/svg' id='Close' width='12' height='12' viewBox='0 0 12 12'>
            <path id='X_5_' d='M6.21,6.5l2.65-2.65c0.19-0.19,0.19-0.51,0-0.71 s-0.51-0.19-0.71,0L5.5,5.79L2.85,3.15c-0.19-0.19-0.51-0.19-0.71,0s-0.19,0.51,0,0.71L4.79,6.5L2.15,9.15 c-0.19,0.19-0.19,0.51,0,0.71c0.19,0.19,0.51,0.19,0.71,0L5.5,7.21l2.65,2.65c0.19,0.19,0.51,0.19,0.71,0 c0.19-0.19,0.19-0.51,0-0.71L6.21,6.5z' fillRule='evenodd' clipRule='evenodd' fill='#666766' />
        </svg>
        </a> : '' }</p>
        </div>
    )
}

// An individual setting. Chnges based on if setting is set or not
class Setting extends React.Component<any, any> {
    render() {
        return (
            <div className='select'>
                <p>Select a {this.props.selecting}</p>
                { this.props.config ? <Selected selecting={this.props.selecting} selected={this.props.selected} /> : <Selector selecting={this.props.selecting} selected={this.props.selected} onClick={this.props.onClick} config={this.props.config} enabled={this.props.enabled} list={this.props.list} onChange={this.props.onChange} /> }
            </div>
        )
    }
}

// This is shown if no parameters are found
function NoParam(props) {
    return (
        <div className='error'>
            Error: You must have an already existing parameter that allows all values.
        </div>
    )
}

// Contains all settings for worksheets
class Relevant extends React.Component<any, any> {
    worksheets() {
        if (this.props.list.length > 0){
            return this.props.list[0].value === '1' && this.props.list[0].displayValue === 'No worksheets found!'
        } else {
            return false;
        }
    }

    render() {
        return (
            <React.Fragment>
            <div className='select'>
                <CheckBoxWithLabelWidget checked={this.props.relevant} disabled={!this.props.configured || this.worksheets()} handleChange={(checked: boolean) => this.props.checkChange(checked)} testId='relevant' label='Show only relevant parameter values' containerStyle={{flexGrow: 1,marginTop:'12px', marginLeft: '-5px'}} />
                <div style={{marginLeft: '21px'}}>
                    <p className={this.props.relevant ? "" : "disabled"}>Select a worksheet to use as filter</p>
                    <div className='controls'>
                        {(this.props.ws_config && this.props.relevant) ? <Selected selecting='worksheet' selected={this.props.selected} clear='clear' clearWS={() => this.props.clearWS()}/> :  <React.Fragment><OutlineDropdown list={this.props.list} currentVal={this.props.selected} disabled={!this.props.enabled || !this.props.relevant} onChange={this.props.onChange}/><ButtonWidget buttonType={ButtonType.Go} handleClick={this.props.setWS} testId={this.props.selecting + 'set'} disabled={!this.props.enabled || !this.props.relevant} style={{marginLeft: '12px'}}>Set</ButtonWidget></React.Fragment>}
                    </div>
                </div>
            </div>
            </React.Fragment>
        )
    }
}

// Contains the controls for changing background color
function Formatting(props) {
    return (
        <React.Fragment>
            <div className='title' style={{marginTop: '30px'}}>
                Formatting
            </div>
            <div className='select'>
                <p>Background Color <input type='color' id='bg' defaultValue={props.bg} onChange={(color) => props.onChange(color.target.value)} style={{backgroundColor: props.bg}}/></p>
            </div>
        </React.Fragment>
    )
}

  
// Creates configuration page with dropdowns etc.
class Settings extends React.Component<any, any> {
    // WANT: ADD A LOADING SPINNER WIDGET FOR CHECKING CONFIGURATION
    
    render(){
        return (
            <React.Fragment>
                <div id='loading' className={this.props.loading ? "loading" : "loaded"}>
                    Checking settings...
                    {/* <ActivitySpinnerWidget testId={'loading'} shouldShowUnderlay={true} /> */}
                </div>
                <div className='container'>
                    <div>
                        <div className='header'>
                            Dynamic Parameter Configuration 
                            <div className='tooltip'>
                                <svg xmlns='http://www.w3.org/2000/svg' id='Dialogs_x5F_Info' width='15' height='15' viewBox='0 0 15 15'>
                                    <rect id='Line' x='7' y='6' width='1' height='5' fillRule='evenodd' clipRule='evenodd' fill='#666766' />
                                    <rect id='Dot_2_' x='7' y='4' width='1' height='1' fillRule='evenodd' clipRule='evenodd' fill='#666766' />
                                    <path id='Circle' d='M7.5,1C3.9,1,1,3.9,1,7.5S3.9,14,7.5,14 S14,11.1,14,7.5S11.1,1,7.5,1z M7.5,13C4.5,13,2,10.5,2,7.5C2,4.5,4.5,2,7.5,2S13,4.5,13,7.5C13,10.5,10.5,13,7.5,13z' fillRule='evenodd' clipRule='evenodd' fill='#666766' />
                                </svg>
                                <span className="tooltiptext">
                                    <b>How to Use</b>
                                    <ol>
                                        <li>Select a Tableau parameter to manipulate</li>
                                        <li>Select a data source that you want to update the parameter from</li>
                                        <li>Select a field to use to populate the parameter</li>
                                        <li><i>Optional:</i> If you only want to see relevant values, select a worksheet to use for filtering. Note: Worksheet must include the field you selected.</li>
                                    </ol>
                                </span>
                            </div>
                        </div>
                        {this.props.tpexists ? <React.Fragment>
                            <div className='title'>Configure Parameter</div>
                            <Setting selecting='parameter' onClick={() => this.props.setParam()} config={this.props.param_config} selected={this.props.parameter} enabled={this.props.param_enabled} list={this.props.param_list} onChange={this.props.paramChange}/> 
                            <Setting selecting='data source' onClick={() => this.props.setDataSource()} config={this.props.ds_config} selected={this.props.datasource} enabled={this.props.ds_enabled} list={this.props.ds_list} onChange={this.props.dsChange}/>
                            <Setting selecting='field' onClick={() => this.props.setField()} config={this.props.field_config} selected={this.props.field} enabled={this.props.field_enabled} list={this.props.field_list} onChange={this.props.fieldChange}/>
                            <div className='title' style={{marginTop: '30px'}}>Values to show</div>
                            <Relevant relevant={this.props.relevant} configured={this.props.configured} enabled={this.props.ws_enabled} selected={this.props.worksheet} setWS={() => this.props.setWS()} list={this.props.ws_list} onChange={this.props.wsChange} checkChange={this.props.checkChange} selecting='worksheet' clear='clear' clearWS={() => this.props.clearWS()} ws_config={this.props.ws_config}/>
                            <Formatting onChange={this.props.bgChange} bg={this.props.bg}/>
                        </React.Fragment> : <NoParam />}
                    </div>

                    <div className='footer'>
                        <div className='btncluster'>
                        <ButtonWidget buttonType={ButtonType.Outline} handleClick={() => this.props.clearSettings()} testId='clear' style={{marginRight: 'auto'}}>Clear Settings</ButtonWidget>
                        <ButtonWidget buttonType={ButtonType.Outline} handleClick={() => this.props.cancel()} testId='cancel'>Cancel</ButtonWidget>
                        <ButtonWidget buttonType={ButtonType.Go} handleClick={() => this.props.submit()} testId='ok' disabled={!this.props.configured || (this.props.relevant && !this.props.ws_config)} style={{marginLeft: '12px'}}>OK</ButtonWidget>
                        </div>
                    </div>

                </div>
            </React.Fragment>
        )
    }
} 

// Container for all configurations
class Configure extends React.Component<any, any> {
    constructor(props) {
        super(props);
        this.state = {
            tpexists: true,
            configured: false,
            param_config: false,
            ds_config: false,
            field_config: false,
            ws_config: false,
            param_enabled: false,
            ds_enabled: false,
            field_enabled: false,
            ws_enabled: false,
            param_list: [],
            ds_list: [],
            field_list: [],
            ws_list: [],
            parameter: '',
            datasource: '',
            field: '',
            worksheet: '',
            relevant: false,
            bg: '#ffffff',
            loading: true,
        };

        this.paramChange = this.paramChange.bind(this);
        this.dsChange = this.dsChange.bind(this);
        this.fieldChange = this.fieldChange.bind(this);
        this.wsChange = this.wsChange.bind(this);
        this.checkChange = this.checkChange.bind(this);
        this.bgChange = this.bgChange.bind(this);
    }

    // Handles checkbox check
    checkChange(state) {
        console.log(`Checkbox changed to ${state}.`);
        this.setState({
            relevant: state,
            ws_enabled: state
        });
    }

    // Handles change in color input
    bgChange(color) {
        console.log(`Set background color to ${color}.`);
        this.setState({
            bg: color
        });
    }

    // Handles selection in worksheet dropdown
    wsChange(wsname) {
        console.log(`Set worksheet to ${wsname}.`);
        this.setState({
            worksheet: wsname
        });
    }

    // Sets which worksheet to use for filters
    setWS() {
        console.log(`Setting worksheet to ${this.state.worksheet}.`);
        if (this.state.worksheet !== '') {
            this.setState({
                ws_config: true,
                ws_enabled: false,
                // relevant: true,
            });
        }
    }

    // Populates list of worksheets
    populateWS() {
        console.log('Populating worksheet list.');
        this.setState({
            worksheet: '1',
            ws_list: [{value: '1', displayValue: 'Loading...'}]
        });
        let options = [];
        let wc = 0;
        let c = 0;
        wslist.forEach(ws => {
            if(ws.sources.find(ds => ds === this.state.datasource)){
                dashboard.worksheets.find(w => w.name === ws.ws).getUnderlyingDataAsync().then(dataTable => {
                    if (dataTable.columns.find(c => c.fieldName === this.state.field)){
                        options.push({
                            value: ws.ws,
                            displayValue: ws.ws
                        });
                        c++;
                    }
                    wc++
                    if(wc === wslist.filter(obj => obj.sources.find(d => d === this.state.datasource)).length){
                        if (c === 0) {
                            this.setState({
                                worksheet: '1',
                                ws_list: [{value: '1', displayValue: 'No worksheets found!'}],
                                ws_enabled: false,
                            });
                        } else {
                            this.setState({
                                worksheet: options[0].value,
                                ws_list: options,
                            });
                        }
                    }
                });
            }
        });
    }

    // Tests if currently set Worksheet to pull filters from exists
    testWSSettings() {
        if (this.state.configured) {
            console.log('Testing worksheet settings.');
            this.setState({loading: true});
            let wsset = window.tableau.extensions.settings.get('selWorksheet');
            if (wsset && window.tableau.extensions.settings.get('dpRelevant') === 'true') {
                if (dashboard.worksheets.find(ws => ws.name === wsset)) {
                    dashboard.worksheets.find(ws => ws.name === wsset).getUnderlyingDataAsync().then(dataTable => {
                        if (dataTable.columns.find(c => c.fieldName === this.state.field)){
                            console.log(`Worksheet ${wsset} exists with field ${this.state.field}.`);
                            this.setState({
                                worksheet: wsset,
                                ws_config: true,
                                ws_enabled: false,
                                loading: false,
                            });
                        } else {
                            console.log(`Worksheet ${wsset} does not have field ${this.state.field} anymore.`);
                            this.setState({loading: false, ws_enabled: true, relevant:false});
                            this.populateWS();
                        }
                    });
                } else {
                    console.log(`Worksheet ${wsset} does not exist.`);
                    this.setState({loading: false, ws_enabled: true, relevant:false});
                    this.populateWS();
                }
            } else {
                console.log('No worksheet was set or relevant filter is off');
                this.setState({loading: false, relevant:false});
                this.populateWS();
            }
        } else {
            this.setState({loading: false});
            this.populateWS();
        }
    }

    // Handles selection in field dropdown
    fieldChange(fname) {
        console.log(`Set field to ${fname}.`);
        this.setState({
            field: fname
        });
    }

    // Sets the field to pull values from for Dynamic Parameter
    setField() {
        console.log(`Setting field to ${this.state.field}.`);
        if (this.state.field !== '') {
            this.setState({
                field_config: true,
                field_enabled: false,
                configured: true,
            });
            this.testWSSettings();
        }
    }

    // Gets list of fields
    populateFieldList() {
        console.log('Populating Field List');
        this.setState({
            field: '1',
            field_list: [{value: '1', displayValue: 'Loading...'}]
        });
        let parameter;
        window.tableau.extensions.dashboardContent.dashboard.getParametersAsync().then(params => {
            parameter = params.find(param => param.name === this.state.parameter);
            return dataSource.getUnderlyingDataAsync();
        })
        .then(columns => {
            let fields = [];
            let options = [];
            let c = 0;
            for (let f of columns.columns) {
                // FIX: Add this back in once data types are working
                // if (f.dataType === parameter.dataType) {
                    options.push({
                        value: f.fieldName,
                        displayValue: f.fieldName
                    });
                    fields.push(f.fieldName);
                    c++;
                // }
            }
            
            if (c === 0) {
                this.setState({
                    field: '1',
                    field_list: [{value: '1', displayValue: 'No fields found!'}],
                    field_enabled: false
                });
            } else {
                this.setState({
                    field: options[0].value,
                    field_list: options,
                    field_enabled: true
                });
            }
        });
    }

    // Tests if currently set Field to pull domain from exists
    testFieldSettings() {
        if (this.state.configured) {
            console.log('Testing field settings.');
            this.setState({loading: true});
            let fset = window.tableau.extensions.settings.get('selField');
            if (fset) {
                dataSource.getUnderlyingDataAsync().then(dataTable => {
                    if (dataTable.columns.find((column) => column.fieldName === fset)) {
                        console.log(`Field ${fset} exists.`);
                        this.setState({
                            field: fset,
                            field_config: true,
                            field_enabled: false,
                            configured: true,
                        });
                        this.testWSSettings();
                    } else {
                        console.log(`Field ${fset} does not exist.`);
                        this.populateFieldList();
                        this.setState({loading: false, configured: false});
                    }
                });
            } else {
                console.log('No field was set');
                this.populateFieldList();
                this.setState({loading: false, configured: false});
            }
        } else {
            this.populateFieldList();
            this.setState({loading: false});
        }
    }

    // Handles selection in data source dropdown
    dsChange(dsname) {
        console.log(`Set data source to ${dsname}.`);
        this.setState({
            datasource: dsname
        });
    }

    // Sets the Data Source to pull values from for Dynamic Parameter
    setDataSource() {
        console.log(`Setting data source to ${this.state.datasource}.`);
        if (this.state.datasource !== '') {
            dataSource = dslist[dsnames.indexOf(this.state.datasource)];
            this.setState({
                ds_config: true,
                ds_enabled: false,
            });
            this.testFieldSettings();
        }
    }

    // Selects data source or shows drop down
    populateDataSourceList() {
        console.log('Populating data source list.');
        this.setState({
            datasource: '1',
            ds_list: [{value: '1', displayValue: 'Loading...'}]
        });
        // if (dslist.length === 1) {
        //     dataSource = dslist[0];
        //     this.setState({
        //         datasource: dslist[0].name,
        //         ds_config: true,
        //         ds_enabled: false,
        //     });
        //     this.testFieldSettings();
        // } else {
            let options = [];
            let c = 0;
            for (let ds of dslist) {
                options.push({
                    value: ds.name,
                    displayValue: ds.name
                });
                c++;
            }
            
            if (c === 0) {
                this.setState({
                    datasource: '1',
                    ds_list: [{value: '1', displayValue: 'No data sources found!'}],
                    ds_enabled: false
                });
            } else {
                this.setState({
                    datasource: options[0].value,
                    ds_list: options,
                    ds_enabled: true
                });
            }
        // }
    }

    // Tests if currently set Data Source exists
    testDataSourceSettings() {
        if (this.state.configured) {
            console.log('Testing data source settings.');
            this.setState({loading: true});
            let dsset = window.tableau.extensions.settings.get('selDataSource');
            if (dsset && dsnames.indexOf(dsset) > -1) {
                console.log(`Data source ${dsset} exists.`);
                dataSource = dslist[dsnames.indexOf(dsset)];
                this.setState({
                    datasource: dsset,
                    ds_config: true,
                    ds_enabled: false,
                });
                this.testFieldSettings();
            } else {
                console.log(`Data source ${dsset} does not exist or was not set.`);
                this.populateDataSourceList();
                this.setState({loading: false, configured: false});
            }
        } else {
            this.populateDataSourceList();
            this.setState({loading: false});
        }
    }
    
    // Gets list of data sources in workbook
    getDS() {
        console.log('Getting data sources in workbook');
        wslist = [];
        dsnames = [];
        dslist = [];
        if(dashboard.worksheets.length === 0){
            this.testDataSourceSettings();
        } else {
            dashboard.worksheets.forEach(worksheet => {
                worksheet.getDataSourcesAsync().then(datasources => {
                    if (wslist.indexOf(worksheet.name) === -1) {
                        wslist.push({
                            ws: worksheet.name,
                            sources: []
                        });
                    }
                    datasources.forEach(datasource => {
                        wslist.find(w => w.ws === worksheet.name).sources.push(datasource.name)
                        if (dsnames.indexOf(datasource.name) === -1) {
                            dsnames.push(datasource.name);
                            dslist.push(datasource);
                        }
                    });
                    if (wslist.length === dashboard.worksheets.length) {
                        this.testDataSourceSettings();
                        console.log(wslist);
                    }
                });
            });
        }
    }

    // Handles selections in parameter dropdown
    paramChange(pname) {
        console.log(`Set parameter to ${pname}.`);
        this.setState({
            parameter: pname
        });
    }

    // Sets which tableau parameter to update
    setParam() {
        console.log(`Setting parameter to ${this.state.parameter}.`);
        if (this.state.parameter !== '') {
            this.setState({
                param_config: true,
                param_enabled: false,
            });
            this.getDS();
        }
    }

    // Gets list of parameters in workbook and populates dropdown
    populateParamList() {
        console.log('Populating parameter list.');
        this.setState({
            parameter: '1',
            param_list: [{value: '1', displayValue: 'Loading...'}]
        });
        window.tableau.extensions.dashboardContent.dashboard.getParametersAsync().then(params => {
            let options = [];
            for (let p of params) {
                if (p.allowableValues.type === 'all') {
                    options.push({
                        value: p.name,
                        displayValue: p.name
                    });
                }
            }
            this.setState({
                parameter: options[0].value,
                param_list: options,
                param_enabled: true
            });

        });
    }

    // Tests if currently set Parameter exists and accepts all values
    testParamSettings() {
        if (this.state.configured) {
            console.log(`Testing parameter setting for ${pset}.`);
            this.setState({loading: true});
            window.tableau.extensions.dashboardContent.dashboard.getParametersAsync().then(params => {
                let testParam = params.find(param => param.name === pset)
                if (testParam) {
                    console.log(`Parameter ${pset} exists.`);
                    if (testParam.allowableValues.type === 'all') {
                        console.log(`Parameter ${pset} is open input.`);
                        this.setState({
                            parameter: testParam.name,
                            param_config: true,
                            param_enabled: false,
                        });
                        this.getDS();
                    } else {
                        console.log(`Parameter ${pset} is not open input.`);
                        this.populateParamList();
                        this.setState({loading: false, configured: false});
                    }
                } else {
                    console.log(`Parameter ${pset} does not exist.`);
                    this.populateParamList();
                    this.setState({loading: false, configured: false});
                }
            });
        } else {
            this.populateParamList();
            this.setState({loading: false});
        }
    }

    // Clears the worksheet configuration and repopulates the list of worksheets
    clearWS() {
        console.log('Clearing worksheet settings.');
        this.setState({
            ws_config: false,
            ws_enabled: true,
            relevant: true
        });
        this.populateWS();
    }

    // Saves settings and closes configure dialog with data source payload
    submit() {
        console.log('Saving configuration.');
        window.tableau.extensions.settings.set('selParam', this.state.parameter);
        window.tableau.extensions.settings.set('selDataSource', this.state.datasource);
        window.tableau.extensions.settings.set('selField', this.state.field);
        window.tableau.extensions.settings.set('selWorksheet', this.state.worksheet);
        window.tableau.extensions.settings.set('dpRelevant', this.state.relevant.toString());
        window.tableau.extensions.settings.set('bg', this.state.bg);
        window.tableau.extensions.settings.set('configured', 'true');
        window.tableau.extensions.settings.saveAsync().then(result => {
            window.tableau.extensions.ui.closeDialog(this.state.datasource);
        });
    }

    // Closes configure dialog with no* payload.
    cancel() {
        console.log('Cancelling configuration.');
        window.tableau.extensions.ui.closeDialog(this.state.configured ? this.state.datasource : 'null');
        // FIX: Change to real null once bug is fixed
    }

    // Clears settings and states
    clearSettings() {
        console.log('Clearing settings.');
        window.tableau.extensions.settings.erase('selParam');
        window.tableau.extensions.settings.erase('selDataSource');
        window.tableau.extensions.settings.erase('selField');
        window.tableau.extensions.settings.erase('selWorksheet');
        window.tableau.extensions.settings.set('dpRelevant', 'false');
        window.tableau.extensions.settings.erase('bg');
        window.tableau.extensions.settings.set('configured', 'false');
        // window.tableau.extensions.settings.saveAsync();
        this.setState({
            configured: false,
            param_config: false,
            ds_config: false,
            field_config: false,
            ws_config: false,
            param_enabled: false,
            ds_enabled: false,
            field_enabled: false,
            ws_enabled: false,
            param_list: [],
            ds_list: [],
            field_list: [],
            ws_list: [],
            parameter: '',
            datasource: '',
            field: '',
            worksheet: '',
            relevant: false,
        });
        console.log(window.tableau.extensions.settings.getAll());
        this.populateParamList();
    }


    // Once we have mounted, we call to initialize
    componentWillMount() {
        let uiPromise = window.tableau.extensions.initializeDialogAsync();
        if (uiPromise) {
            uiPromise.then((openPayload) => {
                this.setState({
                    initialized: true
                });
                console.log(window.tableau.extensions.settings.getAll());
                dashboard = window.tableau.extensions.dashboardContent.dashboard;
                let paramPromise = dashboard.getParametersAsync();
                if (paramPromise) {
                    paramPromise.then(params => {
                        if (params.length > 0) {
                            let c = 0;
                            for (let p of params) {
                                if (p.allowableValues.type === 'all') {
                                    c++;
                                }
                            }
                            if (c > 0){
                                if (window.tableau.extensions.settings.get('configured') === 'true') {
                                    pset = window.tableau.extensions.settings.get('selParam');
                                    let bg = window.tableau.extensions.settings.get('bg');
                                    if (bg) {
                                        this.setState({
                                            bg: bg
                                        });
                                    }
                                    let rel = window.tableau.extensions.settings.get('dpRelevant');
                                    if (rel) {
                                        this.setState({
                                            relevant: (rel === 'true')
                                        });
                                    }
                                    this.setState({
                                        configured: true
                                    });
                                    this.testParamSettings();
                                } else {
                                    console.log('No configuration settings found.');
                                    this.populateParamList();
                                    this.setState({loading: false});
                                }
                            } else {
                                this.setState({
                                    tpexists: false,
                                    loading: false
                                });
                            }
                        } else {
                            this.setState({
                                tpexists: false,
                                loading: false
                            });
                        }
                    });
                }
            });
        } else {
          console.log('Not running inside of window.tableau');
        }
    }

    render() {
      return (
            <Settings 
                submit={() => this.submit()}
                cancel={() => this.cancel()}
                setWS={() => this.setWS()}
                clearWS={() => this.clearWS()}
                clearSettings={() => this.clearSettings()}
                setParam={() => this.setParam()}
                setDataSource={() => this.setDataSource()}
                setField={() => this.setField()}
                paramChange={this.paramChange}
                dsChange={this.dsChange}
                fieldChange={this.fieldChange}
                wsChange={this.wsChange}
                checkChange={this.checkChange}
                bgChange={this.bgChange}
                param_config={this.state.param_config}
                ds_config={this.state.ds_config}
                field_config={this.state.field_config}
                ws_config={this.state.ws_config}
                param_enabled={this.state.param_enabled}
                ds_enabled={this.state.ds_enabled}
                field_enabled={this.state.field_enabled}
                ws_enabled={this.state.ws_enabled}
                configured={this.state.configured}
                parameter={this.state.parameter}
                datasource={this.state.datasource}
                field={this.state.field}
                worksheet={this.state.worksheet}
                param_list={this.state.param_list}
                ds_list={this.state.ds_list}
                field_list={this.state.field_list}
                ws_list={this.state.ws_list}
                tpexists={this.state.tpexists}
                relevant={this.state.relevant}
                bg={this.state.bg}
                loading={this.state.loading}
            />
      );
    }
}

export default Configure