import * as React from 'react';
import { createElement as el } from 'react';

class Home extends React.Component<any, any> {
    render() {
        return (
          <React.Fragment>
          <link rel="stylesheet" type="text/css" media="screen" href="home.css" />
          <div className="icontainer">
            <div className="box">
              <span className="iheader">Data Driven Parameters</span>
              <p>This extension allows you to add a parameter to a Tableau dashboard whose domain is based on your data. Added a new product line? No longer do you need to manually edit the parameter to update it, with this extension your parameter values
                update automatically!</p>
              <p className="big">Installing the Extension</p>
              <p>Download and install the 2018.2 Beta version of Tableau Desktop from the <a href="https://prerelease.tableau.com">Tableau Beta</a> site. Under <strong>Download Beta 2</strong>, click <strong>Tableau Desktop</strong> and download the right
                version for your machine.</p>
              <p>Download the Data Driven Parameter <a href="https://keshiarose.github.io/Data-Driven-Parameter/DataDrivenParameter.trex">manifest file</a>.</p>
              <p className="big">Using the Extension</p>
              <ol>
                <li>Create an open input (All) parameter with a data type matching the field you want to use to populate it</li>
                <li>Drag in a new Extension object to your dashboard</li>
                <li>Find the manifest (.trex) file you downloaded above</li>
                <li>Select the parameter you created above for the extension to manipulate</li>
                <li>Select the data source that holds the field you want to base your parameter on (if only one data source present, the extension will auto select it)</li>
                <li>Select the field you want to base your parameter on</li>
                <li>Optional: If you want your parameter to only contain relevant values, check the box and select the worksheet that has the filters you want to affect the data driven parameter</li>
                <li>Click 'OK'</li>
              </ol>
              <div className="gh"><a href="https://github.com/KeshiaRose/Data-Driven-Parameter">View on GitHub</a></div>
            </div>
          </div>
          </React.Fragment>
        );
    }
}

export default Home