///<amd-module name="ModuleDontChangeThisLine" />
import * as React from 'react';
import { createElement as el } from 'react';

import {
    DropdownWidget,
    DropdownType,
    MenuItemInfo,
    Block,
    InlineBlock,
    TabStyles } from '@tableau/widgets';
  
const { Spacing } = TabStyles;

const Styles = {
    dropdownContainer: {
        marginLeft:0,
        width: '100%'
    }
};
  
interface DropdownState {
    currentVal: string;
}

interface DropdownProps {
    onChange: any;
    list: any;
    currentVal: any;
    disabled: any;
}
  
class OutlineDropdown extends React.Component<DropdownProps, DropdownState>  {
    protected boundOnSelect: (value: string) => void = value => this.props.onChange(value);

    public constructor(props: DropdownProps) {
        super(props);
        this.state = {
            currentVal: this.props.currentVal,
        };
    }

    public render(): JSX.Element {
        return (
            el(
            InlineBlock, {style: Styles.dropdownContainer},
                el(DropdownWidget, {
                    zIndex: 1031,
                    label: 'Dynamic Parameter',
                    containerStyle: {
                        width: '100%'
                    },
                    selectedValue: this.props.currentVal,
                    onSelect: this.boundOnSelect,
                    dropdownType: DropdownType.Outline,
                    maxHeight: 200,
                    testId: 'dp-outline',
                    menuItemInfos: this.props.list,
                    disabled: this.props.disabled
                })
            )
        );
    };
};
  
declare global {
    interface Window { tableau: any; }
}

let dashboard, parameter, dataSource;

class DynamicParameter extends React.Component<any, any> {
    constructor(props) {
        super(props);
        this.state = {
            initialized: false,
            configured: false,
            currentVal: '',
            list: [],
            disabled: true,
        };

        this.populateParam = this.populateParam.bind(this);
        this.getParamData = this.getParamData.bind(this);
        this.updateParam = this.updateParam.bind(this);
        this.wsEvent = this.wsEvent.bind(this);
        this.findParameter = this.findParameter.bind(this);
        this.findDataSource = this.findDataSource.bind(this);
        this.configure = this.configure.bind(this);

    }

    populateParam(dataTable) {
        console.log('Populating parameter.');
        let dataField = window.tableau.extensions.settings.get('selField');
        let field = dataTable.columns.find(column => column.fieldName === dataField);
        if (!field){
            // this.configure();
            this.setState({
                currentVal: '1',
                list: [{value: '1', displayValue: 'Parameter needs configuration'}],
                disabled: true
            });
        } else {
            let fieldIndex = field.index;
            let list = [];
            let options = [];
            for (let row of dataTable.data) {
                list.push(row[fieldIndex].value);
            }
            list = list.filter(function(item, index, inputArray) {
                return inputArray.indexOf(item) === index;
            });
            list.sort();
            for (let l of list) {
                options.push({value: l, displayValue: l});
            }
            let value;
            if (options.find(o => o.value === parameter.currentValue.value)) {
                value = parameter.currentValue.value;
            } else {
                value = options[0].value;
            }
            this.setState({
                currentVal: value,
                list: options,
                disabled: false,
            });
            parameter.changeValueAsync(value);
        }
    }

    // Gets the values from the selected field and populates the Dynamic Parameter
    getParamData() {
        let dataSheet = dashboard.worksheets.find(ws => ws.name === window.tableau.extensions.settings.get('selWorksheet'));
        if (window.tableau.extensions.settings.get('dpRelevant') === 'true' && dataSheet) {
            console.log('Getting only relevant dynamic parameter values.');
            let dataSheet = dashboard.worksheets.find(ws => ws.name === window.tableau.extensions.settings.get('selWorksheet'));
            if (!dataSheet) {
                // this.configure();
                this.setState({
                    currentVal: '1',
                    list: [{value: '1', displayValue: 'Parameter needs configuration'}],
                    disabled: true
                });
            } else {
                dataSheet.getSummaryDataAsync().then(dataTable => {
                    this.populateParam(dataTable);
                });
            }
        } else {
            console.log('Getting all dynamic parameter values.');
            if (!dataSource) {
                // this.configure();
                this.setState({
                    currentVal: '1',
                    list: [{value: '1', displayValue: 'Parameter needs configuration'}],
                    disabled: true
                });
                } else {
                dataSource.getUnderlyingDataAsync().then(dataTable => {
                    this.populateParam(dataTable);
                });
            }
        }
    }

    // Updates the parameter based on selection in Dynamic Parameter
    updateParam(newValue) {
        console.log('Updating parameter.');
        parameter.changeValueAsync(newValue);
        this.setState({currentVal: newValue});
        // this.getParamData();
    }

    // Adds event listener to worksheet i dynamic parameter is using relevant values
    wsEvent() {
        if (window.tableau.extensions.settings.get('dpRelevant') === 'true') {
            let dataSheet = dashboard.worksheets.find(ws => ws.name === window.tableau.extensions.settings.get('selWorksheet'));
            if (!dataSheet) {
                // this.configure();
                this.setState({
                    currentVal: '1',
                    list: [{value: '1', displayValue: 'Parameter needs configuration'}],
                    disabled: true
                });
            } else {
                let unregisterHandlerFunction = dataSheet.addEventListener(window.tableau.TableauEventType.FilterChanged, this.getParamData);
            }
        }
        this.getParamData();
    }

    // Locates the parameter to update
    findParameter() {
        window.tableau.extensions.dashboardContent.dashboard.getParametersAsync().then(params => {
            parameter = params.find(param => param.name === window.tableau.extensions.settings.get('selParam'));
            if (!parameter || parameter.allowableValues.type !== 'all'){
                // this.configure();
                this.setState({
                    currentVal: '1',
                    list: [{value: '1', displayValue: 'Parameter needs configuration'}],
                    disabled: true
                });
            } else {
                this.wsEvent();
            }
        });
    }

    // Gets data source based on name passed back from dialog
    findDataSource(dname) {
        console.log('Collecting data source.');
        this.setState({
            currentVal: '1',
            list: [{value: '1', displayValue: 'Loading...'}]
        });
        let wslist = [];
        if(dashboard.worksheets.length === 0){
            // this.configure();
            this.setState({
                currentVal: '1',
                list: [{value: '1', displayValue: 'Parameter needs configuration'}],
                disabled: true
            });
        } else {
            dashboard.worksheets.forEach(worksheet => {
                worksheet.getDataSourcesAsync().then(datasources => {
                    datasources.forEach(datasource => {
                        if (datasources.find(datasource => datasource.name === dname)) {
                            dataSource = datasources.find(datasource => datasource.name === dname);
                        }
                    });
                    if (wslist.indexOf(worksheet.name) === -1) {
                        wslist.push(worksheet.name);
                    }
                    if (wslist.length === dashboard.worksheets.length) {
                        this.findParameter();
                    }
                });
            });
        }
    }

    // Pops open the configure page
    configure() {
        const popupUrl = `${window.location.origin}/Data-Driven-Parameter/#/config`;
        let payload = "";
        // let h = Math.floor(500 / window.devicePixelRatio);
        // let w = Math.floor(450 / window.devicePixelRatio);
        // console.log(window.devicePixelRatio)
        window.tableau.extensions.ui.displayDialogAsync(popupUrl, payload, { height: 500, width: 450 }).then((closePayload) => {
            console.log('Configure closed.');
            console.log(window.tableau.extensions.settings.getAll());
            document.body.style.backgroundColor = window.tableau.extensions.settings.get('bg');
            if (closePayload !== 'null') { // FIX: Will fix when payload bug is fixed
                this.findDataSource(closePayload);
            } else {
                this.setState({
                    currentVal: '1',
                    list: [{value: '1', displayValue: 'Parameter needs configuration'}],
                    disabled: true
                });
            }
        }).catch((error) => {
            if(window.tableau.extensions.settings.get('configured') !== 'true'){
                this.setState({
                    currentVal: '1',
                    list: [{value: '1', displayValue: 'Parameter needs configuration'}],
                    disabled: true
                });
            }
            switch (error.errorCode) {
                case window.tableau.ErrorCodes.DialogClosedByUser:
                    console.log("Dialog was closed by user.");
                    break;
                default:
                    console.error(error.message);
            }
        });
    }

    // Once we have mounted, we call to initialize
    componentWillMount() {
        let initialziePromise = window.tableau.extensions.initializeAsync({ 'configure': this.configure });
        if (initialziePromise) {
            initialziePromise.then(() => {
                this.setState({
                    initialized: true
                });
                dashboard = window.tableau.extensions.dashboardContent.dashboard;
                let configured = (window.tableau.extensions.settings.get('configured') === 'true');
                if (!configured) {
                    this.configure();
                } else {
                    this.setState({
                        configured: true
                    });
                    this.findDataSource(window.tableau.extensions.settings.get('selDataSource'));
                    document.body.style.backgroundColor = window.tableau.extensions.settings.get('bg');
                }
          });
        }
    }

    render() {
      return (
        <div style={{padding: 0, width: '100%'}}>
            <OutlineDropdown onChange={this.updateParam} list={this.state.list} currentVal={this.state.currentVal} disabled={this.state.disabled}/>
        </div>
      );
    }
}

export default DynamicParameter